*{margin: 0px; padding: 0px;}
label{
    display: none;
}
.login{
    text-align: center;
    width: 500px;
    margin: 0px auto;
    padding-top: 50px;
    position: relative;
    margin-bottom: 50px;
}
.login h2{
    margin-bottom: 30px;
}
#id{
    width: 285px;
    height: 50px;   
    margin-bottom: 10px;
    border: 1px solid rgb(200, 200, 200);
    border-radius: 5px;
    padding: 0px 0px 0px 15px;
}
#pw{
    width: 285px;
    height: 50px;
    border: 1px solid rgb(200, 200, 200);
    border-radius: 5px;
    padding: 0px 0px 0px 15px;
}
.login a{
    color: #333;
}
.login p{
    position: absolute;
    font-size: 12px;
    margin-top: 10px;
}
.find_id{
    left: 49%;
}
.line{
    left: 63%;
}
.find_pw{
    left: 65%;
}
.login_btn{
    width: 305px;
    height: 50px;
    margin-top: 50px;
    background-color: rgb(132, 41, 165);
    border: none;
    color: #fff;
    border-radius: 5px;
}
.join_btn{
    width: 305px;
    height: 50px;
    margin-top: 10px;
    background-color: #fff;
    color: rgb(132, 41, 165);
    border: 2px solid rgb(132, 41, 165);
    border-radius: 5px;
}