/* 신상품 서식 */
.New{
  width: 1050px;
  height: 1800px;
  margin: 0px auto;
}
.New .new_img{
  display: block;
  margin: auto;
}

.New h2{
  text-align: center;
  font-size: 28px;
  margin-top: 50px;
}

/* 상품 서식 */
.shop{
    width: 1050px;
    position: relative;
    margin: 0px auto;
    margin-top: -100px;
    height: 200px;
  }
  .shop h1{
    position: absolute;
    transform: translateY(-70px);
    font-size: 30px;
    left: 38%;
  }
  .shop img{
    margin: 0px auto;
    width: 250px;
    position: absolute; 
  }
  .shop img:first-child{
    left: 0%;
  }
  .shop img:nth-child(2){
    left: 25%;
  }
  .shop img:nth-child(3){
    left: 50%;
  }
  .shop img:last-child{
    left: 75%;
  }
  
